.order__form {
  max-width: 330px;

  @media (max-width: $desktop) {
    max-width: 321px;
  }
}

.order__descr {
  margin: 0;
  padding-top: 6px;
  margin-bottom: 19px;
  font-weight: 600;
  font-size: 17px;
  line-height: 28px;
  text-align: center;
  letter-spacing: -0.005em;
  color: $white;

  @media (max-width: $desktop) {
    color: $black;
    font-size: 18px;
    margin-bottom: 31px;
  }

  @media (max-width: $tablet) {
    margin-bottom: 20px;
  }
}

.order__field {
  input {
    box-sizing: border-box;
    margin-bottom: 20px;
    width: 100%;
    padding: 16px 10px 12px 30px;
    border: 1px solid rgba($black, 0.5);
    border-radius: 6px;
    transition: all 0.3s ease-in-out;
    color: rgba($color: $black, $alpha: 0.7);

    &:hover,
    &:active,
    &:focus {
      outline: none;
      border-color: $color-accent;
      background-color: rgba($color-accent, 0.3);
      color: $white;
    }

    &::placeholder {
      font-size: 15px;
      line-height: 24px;
    }
  }
}

.order__button {
  width: 100%;
  border: none;
  font-weight: 600;
  font-size: 15px;
  line-height: 21px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: $black;
  background-color: $color-accent;
  border-radius: 6px;
  padding: 15px;
  transition: all 0.3s ease-in-out;

  &:hover,
  &:focus {
    background-color: rgba($color-accent, 0.3);
    color: $white;
  }

  &:active {
    background-color: rgba($color-accent, 0.1);
  }
}
