.about-us__title {
  text-align: center;
  margin-bottom: 53px;
}

.about-us__list {
  list-style: none;
  margin: 0;
  padding: 0 19px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @media (max-width: $tablet) {
    justify-content: center;
  }
}

.about-us__item {
  text-align: center;
  margin-bottom: 40px;
}

.about-us__number {
  color: $color-accent;
  font-weight: bold;
  font-size: 28px;
  text-align: center;
  margin-bottom: 20px;
  display: inline-block;
}

.about-us__text {
  padding: 0;
  margin: 0;
  max-width: 263px;
  font-size: 22px;
  line-height: 34px;
  letter-spacing: -0.5px;

  @media (max-width: $tablet) {
    font-size: 18px;
  }
}
